<template>
    <div class="panel">
        <p class="title text-center">
            {{account.hint}}
        </p>
        <p class="btn-copy-token text-center"
           :id="uid" :data-clipboard-target="'#'+uid">
            {{token}}
        </p>
        <div class="progress">
            <div class="progress-bar" role="progressbar" v-bind:style="{width: getProgressBarWidth()}"></div>
        </div>
    </div>
</template>

<script>
import speakeasy from 'speakeasy';
//import Clipboard from 'clipboard';

export default {
  name: 'Account',
  props: {
    account: Object,
    uid: Number
  },
  data() {
    return {
      token: '',
      msToNext: 0,
    }
  },
  created() {
    //new Clipboard('.btn-copy-token');
    setInterval(this.refreshProgressBar, 100);
    this.refreshToken();
  },
  methods: {
    refreshToken() {
      let secret = this.account.secret.replace(/\s+/g, '');
      this.token = speakeasy.totp({
          secret: secret,
          encoding: 'base32'
      });
    },
    refreshProgressBar: function () {
        const now = new Date();
        let nextUpdate = undefined;

        const sec = now.getSeconds();
        if (sec === 0 || sec === 30) {
            this.refreshToken();
        }

        if (sec >= 0 && sec < 30) {
            const middle = new Date(now.getTime());
            middle.setSeconds(30, 0);
            nextUpdate = new Date(middle.getTime());
        } else if (sec >= 30 && sec <= 59) {
            const middle = new Date(now.getTime());
            middle.setSeconds(0, 0);
            nextUpdate = new Date(middle.getTime() + 60000);
        }

        this.msToNext = nextUpdate.getTime() - now.getTime();

        if (this.msToNext <= 0) {
            this.msToNext = 0;
        }
    },
    getProgressBarWidth() {
      return this.msToNext * 100 / 30000 + '%';
    },

  }
}
</script>

