h1 {
        cursor: pointer;
    }

.panel {
    border-radius: 0;
}

.title {
    margin: 0;
    padding: 5px;
    font-weight: bold;
}

.btn-copy-token {
    padding: 10px;
    margin: 0;
    font-size: xx-large;
}

.progress {
    height: 5px;
    border-radius: 0;
    box-shadow: none;
    -webkit-box-shadow: none;
    background-color: transparent;
    margin-bottom: 0;
}

.progress-bar {
  height: 5px;
  background-color: blue;
}

.btn:focus, .btn:active {
    outline: none !important;
}
</style>
